/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #EE9B00;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

#ToggleContainer{
    display: grid;
    grid-template-columns: auto auto auto;
    padding-top: 10px;
    padding-bottom: 10px;
    width: auto;
    border-color: #001219;
    border-width: 5px;
    border-style: solid;

}

#LabelLeft{
    font-weight: bold;
    text-align: left;
    margin-bottom: auto;
    margin-top: auto;
    padding-left: 15px;

}

#LabelRight{
    font-weight: bold;
    text-align: right;
    margin-bottom: auto;
    margin-top: auto;
    padding-right: 15px;
}

#ToggleSwitch {
  margin: auto;
}
